import React from 'react';
import ReactDOM from 'react-dom';

import { AutoComplete, ComboBox, DropDownList, MultiSelect } from '@progress/kendo-react-dropdowns';
import axios from 'axios';

class DfDropDown extends React.Component {

constructor(props) {
    super(props);
    this.state = {
      items: []
    };
  }

componentDidMount() {

    axios.get('//platform.dayforce.com:1111/row-column?row=100&col=1&type=array')
        //.then(res => res.json())
        .then(response => this.setState({ items: 
            response.data.map(obj =>{ 
               return obj.C0;
            })
            })
        );
}    

render() {



        return (
            <div className="example-wrapper" style={{ minHeight: '400px' }}>
                <div className="col-xs-12 col-sm-7 example-col">
                    <p>AutoComplete</p>
                    <AutoComplete data={this.state.items} />
                </div>
                <div className="col-xs-12 col-sm-7 example-col">
                    <p>ComboBox</p>
                    <ComboBox data={this.state.items} />
                </div>
                <div className="col-xs-12 col-sm-7 example-col">
                    <p>DropDownList</p>
                    <DropDownList data={this.state.items} />
                </div>
                <div className="col-xs-12 col-sm-7 example-col">
                    <p>MultiSelect</p>
                    <MultiSelect data={this.state.items} />
                </div>
            </div>
        );
    }
}

export default DfDropDown;


